<template>
	<div id="user">
    <img src="static/img/bg_grzx111.png" class="width_100"/>
    <div class="width_100 head_box">
      <a @click="tel('0516-88995851')">
        <img src="static/img/img_kefu_grzx11.png" style="position: absolute;left: 15px;width: 20px;"/>
        <!--<span class="font_14 sign" style="position: absolute;left: 15px;width: 20px;">签到处</span>-->
      </a>
      <!--<img src="static/img/icon_qiandao_grzx1.png" class="share" @click="toSign"/>-->
      <!--<br>-->
      <span class="font_14 sign" @click="toSign">签到处</span>
    </div>
    <div class="width_100 text_center user_box">
      <img :src="userInfo.userimg" style="height: 94px;width: 94px"/>
      <div class="margin_top_10">
        {{userInfo.username}}
      </div>
    </div>
    <div style="background-color: #ffffff;width: 90%;margin-left: 5%;position: relative;bottom: 50px;border-radius: 5px;border:1px solid #eeeeee">
      <div class="text_right border_bottom_eeeeee" style="padding: 5px;">
        <span style="float: left" class="font_14">我的发布</span>
        <span>&nbsp;</span>
        <span class="font_12 color_c10804" @click="toAuthentication" v-if="show">去认证</span>
        <img src="static/img/icon_yanzmm1.png" style="width: 15px;vertical-align: middle;" @click="toAuthentication" v-if="show"/>
      </div>
      <div style="padding: 5px;">
        <!--<span class="release" @click="toUserService">-->
          <!--<img src="static/img/icon_wx_grzx.png" width="20px">-->
          <!--<br>-->
          <!--<span class="font_12 color_8b8b8b">维修</span>-->
        <!--</span>-->
        <span class="release" @click="toSecondHandService">
          <img src="static/img/icon_mm_grzx.png" width="20px">
          <br>
          <span class="font_12 color_8b8b8b">买卖</span>
        </span>
        <span class="release" @click="toRentalService">
          <img src="static/img/icon_zl_grzx.png" width="20px">
          <br>
          <span class="font_12 color_8b8b8b">租赁</span>
        </span>
        <!--<span class="release" @click="toDispatchingService">-->
          <!--<img src="static/img/icon_ps_grzx.png" width="20px">-->
          <!--<br>-->
          <!--<span class="font_12 color_8b8b8b">配送</span>-->
        <!--</span>-->
        <span class="release" @click="toJobService">
          <img src="static/img/icon_zpqz_grzx.png" width="20px">
          <br>
          <span class="font_12 color_8b8b8b">招聘求职</span>
        </span>
      </div>
    </div>
    <van-cell-group class="user-group" style="margin-top: -40px;">
      <van-cell @click="toUserInfo">
        <img src="static/img/icon_dianyuan--mf.png"/>
        <span class="padding-left-5">个人信息</span>
      </van-cell>
      <div style="background-color: #f4f4f4;height: 5px"></div>
      <van-cell @click="toUserAddress">
        <img src="static/img/icon_tc--h.png"/>
        <span class="padding-left-5">我的地址</span>
      </van-cell>
      <div style="background-color: #f4f4f4;height: 5px"></div>
      <van-cell @click="toUserCollect">
        <img src="static/img/fh12.png"/>
        <span class="padding-left-5">我的收藏</span>
      </van-cell>
      <div style="background-color: #f4f4f4;height: 5px"></div>
      <van-cell @click="toUserFollow">
        <img src="static/img/collect.png"/>
        <span class="padding-left-5">我的关注</span>
      </van-cell>
      <div style="background-color: #f4f4f4;height: 5px"></div>
      <van-cell @click="toBrowsingHistory">
        <img src="static/img/liulanlishi.png"/>
        <span class="padding-left-5">浏览历史</span>
      </van-cell>
      <div style="background-color: #f4f4f4;height: 5px"></div>
      <van-cell @click="toSetting">
        <img src="static/img/setting.png"/>
        <span class="padding-left-5">设置</span>
      </van-cell>
    </van-cell-group>
	</div>
</template>

<script>
	export default {
		name: 'user',
		data() {
			return {
			  show:true,
        userInfo:[],
        userId:localStorage.getItem('userId')
			}
		},
    mounted() {
		  this.getUserInfo();
    },
		methods: {
      tel(telNum){
        dsBridge.call('dialingTel',telNum);
      },
		  getUserInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("user/getuserinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.userInfo = rpdata.data.data;
            if(rpdata.data.data.primary=='Y' && rpdata.data.data.senior=='Y'){
              $this.show = false;
            }
          }
        })
        .catch(function(err) {})
      },
      //签到
      toSign(){
        this.$router.push('sign');
      },
      //发布招聘求职
      toJobService(){
        this.$router.push('jobService');
      },
      //发布配送
      toDispatchingService(){
        this.$router.push('dispatchingService');
      },
      //发布出租房
      toRentalService(){
        this.$router.push('RentalService');
      },
      //发布卖二手房
      toSecondHandService(){
        this.$router.push('secondHandService');
      },
      //我的维修
      toUserService(){
        this.$router.push('userService');
      },
      //我的地址
      toUserAddress(){
        this.$router.push('userAddress');
      },
      //我的信息
      toUserInfo(){
        this.$router.push('userInfo');
      },
      //设置
      toSetting(){
        this.$router.push('setting');
      },
      //浏览历史
      toBrowsingHistory(){
        this.$router.push('browsingHistory');
      },
      //我的收藏
      toUserCollect(){
        this.$router.push('userCollect');
      },
      //我的关注
      toUserFollow(){
        this.$router.push('userFollow');
      },
      //认证
      toAuthentication(){
        this.$router.push('authentication');
      },
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #user{
    margin-bottom: 55px;
  }
  .barImg {
    width: 20px;
    vertical-align: middle;
  }
  .release {
    /*width: 18.7%;*/
    width: 32.0%;
    display: inline-block;
    text-align: center;
  }
  .sign {
    position: absolute;
    right: 8px;
  }
  .user_box {
    position: absolute;
    top: 40px;
  }
  .head_box {
    position: absolute;
    top: 10px;
  }
  .share {
    position: absolute;
    right: 15px;
    width: 20px;
  }
</style>



